import axios from "axios";
import { useState } from "react";

const App = () => {
  const [linkMonth, setMonth] = useState("");
  const [linkTag, setTag] = useState("");
  const [linkName, setName] = useState("");
  const [linkUrl, setUrl] = useState("");

  const handleMonthChange = (event) => {
    setMonth(event.target.value);
  };

  const handleTagChange = (event) => {
    setTag(event.target.value);
  };

  const handleNameChange = (event) => {
    setName(event.target.value);
  };

  const handleUrlChange = (event) => {
    setUrl(event.target.value);
  };

  const addNote = (event) => {
    event.preventDefault();
    const newLink = {
      tag: linkTag,
      name: linkName,
      url: linkUrl,
      month: linkMonth,
    };
    console.log("newlink frontend", newLink);
    axios
      .put(`/months`, newLink)
      .then((response) => {
        console.log(response);
        console.log("posted link to the server");
      })
      .catch((error) => console.log(error));
    console.log(linkMonth, linkTag, linkName, linkUrl);
  };

  return (
    <div className="App">
      <form onSubmit={addNote}>
        <label>Enter month</label>
        <br />
        <input value={linkMonth} onChange={handleMonthChange} />
        <br />
        <label>Enter tag</label>
        <br />
        <input value={linkTag} onChange={handleTagChange} />
        <br />
        <label>Enter name</label>
        <br />
        <input value={linkName} onChange={handleNameChange} />
        <br />
        <label>Enter url</label>
        <br />
        <input value={linkUrl} onChange={handleUrlChange} />
        <br />
        <button type="submit">Save note</button>
      </form>
    </div>
  );
};

export default App;
